<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="付款时间" v-model="queryData.paymentTime" template="daterange">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="渠道">
                            <el-select size="small" v-model="queryData.fulfillment_type" filterable>
                                <el-option v-for="item in optionList.fulfillmentList"
                                           :key="item.value"
                                           :value="item.value"
                                           :label="item.label"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement v-model="queryData.summary_type" label="统计类型" template="select" :options="optionList.countTypeList">
                        </SearchRequirement>
                    </li>
                </ul>
                <FilterBtn refTable="tableArea" :params="{group_id: ''}" :query-fn="handleQueryInfo" :reset-fn="handleResetQueryInfo"></FilterBtn>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon
                    :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
        </div>
        <ComTableInfo
                rowKey="id"
                :tableKey="tableKey"
                :tableProps="tableProps"
                :tableData="tableData"
                :tableWidth="['120']"
                :showSearch="showSearch"
                :otherHeight="['ui-main-tab']"
                :load="handleTableOpen">
            <div class="ui-main-tab">
                <el-badge
                        v-for="(item, index) in optionList.moduleTypeList"
                        :key="index">
                    <li class="ui-main-tab-box">
                        <span @click="handleToggleTableInfo(item.value)"
                              :class="queryData.query_type === item.value && 'ui-active'">{{item.label}}<i
                                class="ui-tab_line"></i></span>
                    </li>
                </el-badge>
            </div>
        </ComTableInfo>
    </div>
</template>

<script>
    import config from '@/assets/js/config';
    import {
        autoAddTableInx,
        getDefaultTime,
        changeTableInfoProps,
        goToNewPage,
        getThirtyDayDateRange
    } from '@/services/index';
    import {
        mapActions,
        mapGetters
    } from 'vuex';
    import {
        getSalesPerformanceCountInfo,
    } from '@/api/am_order_count/sales_performance_count';

    export default {
        data () {
            return {

                // 时间搜索框宽度设置
                searchDateWidth: config.SEARCH_DATE_WIDTH,

                thisVm: {},

                // 搜索缩进
                showSearch: true,

                queryData: {
                    group_id: '',
                    group_ids: '',
                    account_id: '',
                    account_ids: '',
                    fulfillment_type: '',
                    summary_type: 'd',
                    paymentTime: [],
                    query_type: 'sales'
                },
                saveDefaultTime: [],

                optionList: {
                    countTypeList: {
                        'd': '天'
                    },
                    fulfillmentList: [],
                    moduleTypeList: [
                        {
                            label: '销售额统计',
                            value: 'sales',
                        },
                        {
                            label: '订单量统计',
                            value: 'orders'
                        }
                    ]
                },

                tableData: [],
                tableKey: [],
                tableProps: [],

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                }
            };
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'salesPerformanceCountInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetSalesPerformanceCountInfo'
            ]),
            autoAddTableInx,
            getDefaultTime,
            goToNewPage,

            // 列表信息查询
            handleQueryInfo (params = {}, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['sales_performance_count']);
                }

                let obj = {
                    ...this.queryData,
                    start_date: this.queryData.paymentTime[0],
                    end_date: this.queryData.paymentTime[1],
                    ...params
                };

                this.GetSalesPerformanceCountInfo(obj).then(({data}) => {

                    // 导出
                    if (data.status === 2) return;

                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },

            // 查询重置
            handleResetQueryInfo () {
                this.queryData.group_id = '';
                this.queryData.group_ids = '';
                this.queryData.account_id = '';
                this.queryData.account_ids = '';
                this.queryData.fulfillment_type = '';
                this.queryData.summary_type = 'd';
                this.queryData.paymentTime = this.saveDefaultTime;
            },

            //高级筛选
            handleClickFilter () {
                this.FilterShow = !this.FilterShow;
            },

            // 分页条数切换
            handleSizeChange (val) {
                this.handleQueryInfo({
                    limit: val,
                    offset: 1
                }, true);
            },

            // 跳转页数切换
            handleCurrentChange (val) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: val
                }, true);
            },

            handleToggleTableInfo(val) {
                this.queryData.group_id = '';
                this.queryData.query_type = val;
                this.handleQueryInfo();
            },
            
            // 展开表格
            handleTableOpen({group_id, id}, treeNode, resolve) {
                this.queryData = JSON.parse(this.queryParamsInfo['sales_performance_count']);
                this.queryData.group_id = group_id;
                let obj = {
                    ...this.queryData,
                    start_date: this.queryData.paymentTime[0],
                    end_date: this.queryData.paymentTime[1]
                };

                getSalesPerformanceCountInfo(obj).then(({data}) => {
                    resolve(data.data.value.map((_v, _i) => {
                        return {
                            ..._v,
                            group_name: '',
                            id: `${id}_${_i}`
                        }
                    }))
                }).catch(err => {
                    console.log(err);
                });
            },

            // 操作回调
            handleOperationCallBack (data, successFn, errorFn) {
                if (data.status) {
                    this.$message.success('操作成功！');
                    this.handleQueryInfo({limit: this.pageData.currentList});
                    successFn && successFn();
                } else {
                    this.$message.error(data.errorMess || '操作失败！');
                    errorFn && errorFn();
                }
            },

            //查询回调
            handleQueryInfoCallBack ({data}) {

                this.optionList.countTypeList = data.summary_type_list
                this.optionList.fulfillmentList = data.fulfillment_list

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'sales_performance_count',
                    value: JSON.stringify(this.queryData)
                });

                //数据渲染
                this.tableData = data.value.map((_v, index) => {
                    return {
                        ..._v,
                        id: index + 1,
                        hasChildren: !!index
                    }
                });

                this.tableKey = data.key;
                const props = [...data.key];
                props.splice(0, 3)
                this.tableProps = ['group_name', 'user_name', this.queryData.query_type === 'sales' ? 'total_price' : 'total_order_sum', ...props];
            }
        },
        created () {
            this.thisVm = this;
            try {
                if (JSON.stringify(this.salesPerformanceCountInfo) === '{}') {
                    this.queryData.paymentTime = getThirtyDayDateRange(new Date(new Date().getTime() - 86400000));
                    this.saveDefaultTime = getThirtyDayDateRange(new Date(new Date().getTime() - 86400000));
                    this.handleQueryInfo();
                } else {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['sales_performance_count']);
                    this.saveDefaultTime = this.queryData.paymentTime;
                    this.handleQueryInfoCallBack(this.salesPerformanceCountInfo);
                }
            } catch (e) {
                console.log(e);
            }
        },
    };
</script>

<style lang="less" scoped>

</style>



