import CONFIG from '@/assets/js/config';
import http from '@/http';
export function getAccountSalesVolumeInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/amazon/Sale_summary/getSaleSummaryByAccount', data);
}



