<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="付款时间" v-model="queryData.paymentTime" template="daterange" :pickerOptions="pickerOptions">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="渠道">
                            <el-select size="small" v-model="queryData.fulfillment_type" filterable>
                                <el-option v-for="item in optionList.fulfillmentList"
                                           :key="item.value"
                                           :value="item.value"
                                           :label="item.label"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement v-model="queryData.summary_type" label="统计类型" template="select" :options="optionList.countTypeList">
                        </SearchRequirement>
                    </li>
                </ul>
                <FilterBtn refTable="tableArea" :params="{type: 'all'}" :query-fn="handleNodeClick" :reset-fn="handleResetQueryInfo"></FilterBtn>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon
                    :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
        </div>
        <TemplateDefault :staticNum="120" color="#FFFFFF">
            <div class="ui-content_area clearFix">
                <div class="ui-content_area--left">
                    <el-tree :data="groupTree" :props="groupTreeProps"
                             :expand-on-click-node="false"
                             @node-click="(v) => { handleNodeClick(v, 'click') }"
                             @node-expand="(v) => { handleNodeClick(v, 'on') }"
                             @node-collapse="(v) => { handleNodeClick(v, 'off') }"></el-tree>
                </div>
                <div class="ui-content_area--main">
                    <ComEchart :ref="item.target" v-for="(item, index) in chartData" width="100%" :key="index" :data="item"></ComEchart>
                </div>
            </div>
        </TemplateDefault>
    </div>
</template>

<script>
    import config from '@/assets/js/config';
    import {
        autoAddTableInx,
        getDefaultTime,
        changeTableInfoProps,
        goToNewPage,
        getThirtyDayDateRange
    } from '@/services/index';
    import {
        mapActions,
        mapGetters
    } from 'vuex';
    import {
        getSalesVolumeFormsInfo
    } from '@/api/am_order_count/sales_volume_forms';
    import {
        getGroupOptionList
    } from '@/api/common';

    export default {
        data () {
            return {

                // 时间搜索框宽度设置
                searchDateWidth: config.SEARCH_DATE_WIDTH,

                thisVm: {},

                // 搜索缩进
                showSearch: true,
                saveDefaultTime: [],
                queryData: {
                    group_id: '',
                    group_ids: '',
                    account_id: '',
                    account_ids: '',
                    fulfillment_type: '',
                    summary_type: 'd',
                    paymentTime: []
                },
                kevinId: '',

                optionList: {
                    countTypeList: {
                        'd': '日'
                    },
                    fulfillmentList: []
                },

                tableData: [],
                tableKey: [],
                tableProps: [],

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                },

                // 小组Tree
                groupTree: [],
                groupTreeProps: {
                    label: 'value',
                    value: 'key'
                },

                chartData: [],
                queryChartData: {},

                pickerOptions: {
                    disabledDate(time) {
                        return new Date(time).getTime() < 1514678400000
                    }
                }
            };
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'storeKey'
            ])
        },
        methods: {
            ...mapActions([
                'Fn'
            ]),
            autoAddTableInx,
            getDefaultTime,
            goToNewPage,
            getSalesVolumeFormsInfo,
            getGroupOptionList,

            // 列表信息查询
            handleQueryInfo (params, isFn, item, success) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['only_key']);
                }

                let obj = {
                    ...this.queryData,
                    start_date: this.queryData.paymentTime[0],
                    end_date: this.queryData.paymentTime[1]
                };

                this.getSalesVolumeFormsInfo(obj).then(({data}) => {
                    if (!data.status) {
                        this.$message.error(data.errorMess || '查询失败！');
                        return;
                    }

                    this.handleQueryInfoCallBack(data, item, success, params);
                }).catch(err => {
                    console.log(err);
                });
            },

            // 查询重置
            handleResetQueryInfo () {
                this.queryData = {
                    group_id: '',
                    group_ids: '',
                    account_id: '',
                    account_ids: '',
                    fulfillment_type: '',
                    summary_type: 'd',
                    paymentTime: this.saveDefaultTime
                }
            },

            //高级筛选
            handleClickFilter () {
                this.FilterShow = !this.FilterShow;
            },

            // 分页条数切换
            handleSizeChange (val) {
                this.handleQueryInfo({
                    limit: val,
                    offset: 1
                }, true);
            },

            // 跳转页数切换
            handleCurrentChange (val) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: val
                }, true);
            },

            // 树形节点被点击
            handleNodeClick(item, type = 'on') {

                // 点击查询按钮
                if (item && item.type === 'all') {
                    this.queryData.group_id = '';
                    this.queryData.group_ids = '';
                    this.queryData.account_id = '';
                    this.queryData.account_ids = '';
                    const treeData = [...this.groupTree];
                    this.groupTree.splice(0);
                    this.handleQueryInfo({type: 'all'}, false, false, () => {
                        this.queryData.group_id = this.kevinId;
                        this.groupTree = treeData;
                        this.handleQueryInfo();
                    });
                    return;
                }

                // 点击树形节点名称
                if (type === 'click') {
                    const areaEl = document.querySelector('.ui-content_area--main');
                    if (item.key === '0') {
                        areaEl.scrollTop = 0;
                    } else {
                        const itemTop = this.$refs[`${item.type}_${item.key}`][0].$el.getBoundingClientRect().top;
                        const areaTop = areaEl.scrollTop;
                        areaEl.scrollTop = areaTop + itemTop - 150;
                    }

                    return;
                }

                // 展开一级节点
                if (item.key === '0' && type === 'on') {
                    let ids = item.children.map(_v => {
                        return _v.key;
                    })

                    this.queryData.group_id = '';
                    this.queryData.group_ids = ids.join(',');
                    this.handleQueryInfo();
                } else if (item.children && item.children.length && type === 'on'){
                    this.queryData.group_id = item.key;
                    this.queryData.group_ids = '';
                    let ids = item.children.map(_v => {
                        return _v.key
                    })
                    this.queryData.account_ids = ids.join(',');
                    this.handleQueryInfo({}, false, item);
                } else if (item.key === '0' && type === 'off'){
                    this.queryData.group_ids = '';
                    this.queryData.group_id = '';
                    this.queryData.account_ids = '';
                    this.queryData.account_id = '';
                    this.chartData = [this.chartData[0], this.chartData[1]];
                } else if (type === 'off' && this.queryChartData[item.key]) {
                    let chartData = [...this.chartData];
                    for (let i = 0; i < this.chartData.length; i++) {
                        const _v = this.chartData[i];
                        if (_v.original_name === item.value) {
                            chartData.splice(i + 1, this.queryChartData[item.key].length);
                            delete this.queryChartData[item.key];
                            break;
                        }
                    }
                    this.chartData.splice(0);
                    this.$nextTick().then(() => {
                        this.chartData = [...chartData]
                    })
                }
            },

            // 限制时间
            handleDisTime(date) {
                return false
            },

            // 操作回调
            handleOperationCallBack (data, successFn, errorFn) {
                if (data.status) {
                    this.$message.success('操作成功！');
                    this.handleQueryInfo();
                    successFn && successFn();
                } else {
                    this.$message.error(data.errorMess || '操作失败！');
                    errorFn && errorFn();
                }
            },

            //查询回调
            handleQueryInfoCallBack ({ data }, item, success, params) {

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'only_key',
                    value: JSON.stringify(this.queryData)
                });

                // this.queryChartData = Array.isArray(data) ? data : [data];
                if (item) {
                    let arr = [...this.chartData];
                    for(let i = 0; i < this.chartData.length; i++) {
                        const _v = this.chartData[i];
                        if (_v.original_name === item.value) {
                            arr.splice(i + 1, 0, ...data);
                            break;
                        }
                    }
                    this.queryChartData[item.key] = data;

                    this.chartData.splice(0);
                    this.$nextTick().then(() => {
                        this.chartData = [...arr]
                    })
                } else if (params && params.type === 'all') {
                    this.chartData.splice(0);
                    this.$nextTick().then(() => {
                        this.chartData = [data];
                    })
                } else {
                    const chartData = Array.isArray(data) ? [...this.chartData, ...data] : [...this.chartData, data];
                    this.chartData.splice(0);
                    this.$nextTick().then(() => {
                        this.chartData = [...chartData];
                    })
                }

                success && success();
            }
        },
        created () {
            this.thisVm = this;
            this.getGroupOptionList().then(({ data }) => {

                this.optionList.countTypeList = data.data.summary_type_list;
                this.optionList.fulfillmentList = data.data.fulfillment_list;

                let index = 0;
                let kevinId = '';
                let kevinItem = {};
                for (let item of data.data.category_list) {
                    if (item.value === 'Kevin小组') {
                        kevinId = item.key;
                        kevinItem = item;
                        break;
                    }
                    index++;
                }
                data.data.category_list.splice(index, 1);

                this.groupTree = [
                    {
                        value: '所有小组',
                        key: '0',
                        type: 'group',
                        children: data.data.category_list
                    },
                    kevinItem
                ]

                // 默认时间
                this.queryData.paymentTime = getThirtyDayDateRange(new Date(new Date().getTime() - 86400000));
                this.saveDefaultTime = getThirtyDayDateRange(new Date(new Date().getTime() - 86400000));

                this.handleQueryInfo(false, false, false, () => {
                    this.queryData.group_id = kevinId;
                    this.kevinId = kevinId;
                    this.handleQueryInfo();
                });
            })
        },
    };
</script>

<style lang="less" scoped>
    .ui-content_area {
        position: relative;
        height: 100%;
        .ui-content_area--left {
            /*float: left;*/
            width: 200px;
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            overflow: auto;
            z-index: 1;
            border-right: 1px solid #e6e6e6;
        }
        .ui-content_area--main {
            position: relative;
            height: 100%;
            overflow: auto;
            padding-left: 210px;
            width: 100%;
            box-sizing: border-box;
        }
    }
</style>



