<template>
    <div class="ui-echart" ref="myEchart" :style="{height, width}"></div>
</template>

<script>

    let option = {
        color: ['#FF201B', '#F48314'],
        title: {
            text: '深圳小组'
        },
        tooltip : {
            trigger: 'axis',
            axisPointer: {
                type: 'cross',
                label: {
                    backgroundColor: '#6a7985'
                }
            }
        },
        legend: {
            data:['销售额','订单量']
        },
        toolbox: {
            feature: {
                saveAsImage: {}
            }
        },
        grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
        },
        xAxis : [
            {
                type : 'category',
                boundaryGap : false,
                data : ['周一','周二','周三','周四','周五','周六','周日']
            }
        ],
        yAxis : [
            {
                type: 'value',
                axisLabel : {
                    show : true,
                    formatter: function (value) {
                        const len = value.toString().length;
                        const maxLen = 10;
                        const newLeng = '  '.repeat(maxLen - len);
                        return newLeng + value;
                    }
                }
            },
            {
                type: 'value',
                min: 0,
                axisLabel : {
                    show : true,
                    formatter: function (value) {
                        const len = value.toString().length;
                        const maxLen = 10;
                        const newLeng = '  '.repeat(maxLen - len);
                        return value + newLeng;
                    }
                }
            }
        ],
        series : [
            {
                name:'销售额',
                type:'line',
                areaStyle: {
                    color: '#FF201B'
                },
                lineStyle: {
                    color: '#FF201B'
                },
                data:[120, 132, 101, 134, 90, 230, 210],
                smooth: true,
                yAxisIndex: 0,
            },
            {
                name:'订单量',
                type:'line',
                areaStyle: {
                    color: '#F48314'
                },
                lineStyle: {
                    color: '#F48314'
                },
                data:[220, 182, 191, 234, 290, 330, 110],
                smooth: true,
                yAxisIndex: 1
            }
        ]
    };

    import echarts from 'echarts';
    export default {
        name: "ComEchart",
        props: {
            height: {
                type: String,
                default: '400px'
            },
            width: {
                type: String,
                default: '100%'
            },
            data: {
                type: [Array, Object]
            }
        },
        data() {
            return {
                ECharts: {},
                isOn: false,
                isDispose: false
            }
        },
        methods: {
            init(option) {
                this.ECharts = echarts.init(this.$refs.myEchart);
                this.ECharts.setOption(option);
            },
            refresh(chart) {
                if (chart) {
                    let item = [[], [], []];
                    chart.forEach(_v => {
                        item[0].push(_v.total_price);
                        item[1].push(_v.total_order_sum);
                        item[2].push(_v.date);
                    })

                    option.series = option.series.map((_v, _i) => {
                        return {
                            ..._v,
                            data: item[_i]
                        }
                    });
                    option.xAxis[0].data = item[2];
                    option.yAxis[1].max = this.data.data_max.max_total_order_sum * 2;
                    option.title.text = this.data.name;
                    this.init(option)
                } else {
                    this.ECharts.dispose && this.ECharts.dispose();
                    this.isOn = false;
                }
            },
            scroll() {
                const offsetTop = this.$refs.myEchart.getBoundingClientRect().top;
                if (offsetTop < 900 && offsetTop > -900 && !this.isOn) {
                    this.refresh(this.data.chart);
                    this.isOn = true;
                } else if ((offsetTop > 900 || offsetTop < -900) && this.isOn) {
                    this.refresh();
                }
            }
        },
        mounted() {
            this.$nextTick(() => {
                let areaEle = document.querySelector('.ui-content_area--main');
                areaEle.addEventListener('scroll', this.scroll);
            })
            this.scroll();
        },
        watch: {
            data({ chart }) {
                // this.refresh(chart);
            }
        },
        beforeDestroy() {
            let areaEle = document.querySelector('.ui-content_area--main');
            areaEle && areaEle.removeEventListener('scroll', this.scroll);
        }
    }
</script>

<style scoped>

</style>


