import CONFIG from '@/assets/js/config';
import http from '@/http';
export function getSalesPerformanceCountInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/amazon/Sale_summary/getSalesPerformance', data);
}


