<template>
    <div class="ui-echart" ref="myEchart" :style="{height, width}"></div>
</template>

<script>

    let option = {
        color: [],
        title: {
            text: ''
        },
        tooltip: {
            trigger: 'axis',
            axisPointer: {
                type: 'cross',
                label: {
                    backgroundColor: '#6a7985'
                }
            }
        },
        legend: {
            data: []
        },
        toolbox: {
            feature: {
                saveAsImage: {}
            }
        },
        grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
        },
        xAxis: [
            {
                type: 'category',
                boundaryGap: false,
                data: [],
            }
        ],
        yAxis: [
            {
                name: '',
                type: 'value',
                axisLabel: {
                    formatter: '{value} %'
                },
                nameTextStyle: {
                    color: '#FF2E2F'
                },
                axisLine: {
                    lineStyle: {
                        color: '#FF2E2F'
                    }
                }
            }
        ],
        series: [
            {
                data: [],
                yAxisIndex: 0,
            }
        ]
    };

    import echarts from 'echarts';

    export default {
        name: "ComEchart",
        props: {
            height: {
                type: String,
                default: '400px'
            },
            width: {
                type: String,
                default: '100%'
            },
            data: {
                type: [Array, Object]
            },
            dataKey: {
                type: String,
                default: ''
            },
            title: {
                type: String,
                default: '统计表'
            },
            yName: {
                type: String,
                default: ''
            },
            unit: {
                type: String,
                default: ''
            }
        },
        data() {
            return {
                ECharts: {},
                isOn: false,
                isDispose: false
            }
        },
        methods: {
            init(option) {
                this.ECharts = echarts.init(this.$refs.myEchart);
                this.ECharts.setOption(option);
            },
            refresh(chart) {
                if (chart) {
                    let item = [[], [], []];
                    let series = [];
                    chart.forEach(_v => {
                        item[0].push(_v.total_price);
                        item[2].push(_v.date);
                        _v.list.forEach((_vs, _is) => {
                            if (!series[_is]) {
                                series[_is] = {
                                    name: _vs.site,
                                    type: 'line',
                                    // stack: '统计',
                                    data: [_vs.percent]
                                }
                                option.color.push(_vs.color);
                                option.legend.data.push(_vs.site);
                            } else {
                                series[_is].data.push(_vs.percent);
                            }
                        });
                    })

                    option.series = series;
                    option.xAxis[0].data = item[2];
                    option.title.text = this.title;
                    option.yAxis[0].name = this.yName;
                    option.yAxis[0].axisLabel.formatter = `{value} ${this.unit}`;
                    this.init(option)
                } else {
                    this.ECharts.dispose && this.ECharts.dispose();
                }
            },
            scroll() {
                this.refresh(this.data.chart);
            }
        },
        mounted() {
            this.$nextTick(() => {
                let areaEle = document.querySelector('.ui-content_area--main');
                areaEle.addEventListener('scroll', this.scroll);
            })
            this.scroll();
        },
        watch: {
            data({chart}) {
                this.refresh(chart);
            }
        },
        beforeDestroy() {
            let areaEle = document.querySelector('.ui-content_area--main');
            areaEle && areaEle.removeEventListener('scroll', this.scroll);
        }
    }
</script>

<style scoped>

</style>


